:root {
  --primary-color: #2197b9;
  --dark-mode-highlight-black: color(srgb 0.0768 0.0768 0.0768);
}

.primary-text {
  color: var(--primary-color);
}

.table-actions .anticon {
  color: var(--primary-color);
}

.login-header-container {
  padding-bottom: 40px;
}

body {
  margin: 0 !important;
}

.light-mode {
  .ant-layout {
    background: #f6f8fe;
  }
}

.login-header-container .logo-img {
  margin: auto;
  display: flex;
  width: 200px;
}

.hand {
  cursor: pointer;
}

.app-header {
  padding: 0 10px 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 1000;
  width: 100%;
}

.light-mode .app-header {
  background: #fff !important;
}

.app-sidebar {
  overflow: auto;
  height: 100vh;
  position: sticky;
  inset-inline-start: 0;
  top: 0;
  bottom: 0;
  scrollbar-width: thin;
  scrollbar-gutter: stable;
}

.app-logo-container {
  position: sticky;
  z-index: 1000;
  top: 0;
}

.app-logo-container a img {
  height: 28px;
}

.app-layout-container {
  margin: 10px;
  padding: 10px;
}
.app-logo-container {
  display: flex;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px dashed #20b75761;
  margin-bottom: 21px;
}

.light-mode .app-logo-container,
.light-mode .app-layout-container {
  /* background: #fff !important; */
}

.blinking {
  -webkit-animation: 1s blink ease infinite;
  -moz-animation: 1s blink ease infinite;
  -ms-animation: 1s blink ease infinite;
  -o-animation: 1s blink ease infinite;
  animation: 1s blink ease infinite;
}

@keyframes blink {
  from,
  to {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

@-moz-keyframes blink {
  from,
  to {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

@-webkit-keyframes blink {
  from,
  to {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

@-ms-keyframes blink {
  from,
  to {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

@-o-keyframes blink {
  from,
  to {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }
}

/**
Ant-Overrides
**/
.ant-input-outlined {
  border-radius: 32px;
}

.ant-btn {
  border-radius: 32px;
}

.ant-layout-sider-children {
  padding: 10px;
}

.ant-input-lg {
  font-size: 16px !important;
}
.ant-btn-lg {
  font-size: 16px !important;
}

.ant-menu-title-content {
  font-size: 14px;
}

.ant-menu-submenu-selected {
  background: #20b75715;
}

.ant-menu-light .ant-menu-submenu-selected > .ant-menu-submenu-title,
.ant-menu-light
  > .ant-menu
  .ant-menu-submenu-selected
  > .ant-menu-submenu-title {
  background: #20b757;
  color: #fff;
  .ant-menu-item-icon {
    color: #fff !important;
  }
}

.ant-menu-item-icon {
  color: #20b757 !important;
  font-size: 18px;
}
.ant-menu-light.ant-menu-inline .ant-menu-sub.ant-menu-inline {
  background: transparent;
}

.ant-select-single .ant-select-selector {
  border-radius: 32px;
  font-size: 14px;
}
.ant-menu-light.ant-menu-root.ant-menu-inline,
.ant-menu-light.ant-menu-root.ant-menu-vertical {
  border-inline-end: none;
}

.ant-menu-inline-collapsed {
  .ant-menu-submenu {
    .ant-menu-submenu-title {
      border-radius: 50%;
      height: 52px;
      line-height: 54px;
      .ant-menu-item-icon {
        font-size: 22px;
      }
    }
  }
  .ant-menu-submenu-selected {
    background: transparent;
  }
}

.ant-menu-inline-collapsed > .ant-menu-submenu > .ant-menu-submenu-title {
  padding-inline: calc(50% - 11px - 4px);
}
