.summary-card {
  .card {
    padding: 10px 14px;
    border-radius: 12px;
    min-width: 23%;
    .title {
      font-size: 14px;
      border-bottom: 1px dashed #20b75761;
      padding-bottom: 10px;
      margin-bottom: 10px;
      font-weight: 500;
    }

    .data {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .value {
        .number {
          font-size: 18px;
          font-weight: 500;
        }
        .desc {
          margin-top: 4px;
          font-size: 14px;
          display: flex;
          font-weight: 300;
        }
        .negative {
          color: #ff6161;
        }
        .positive {
          color: #20b757;
        }
      }
      .graph {
        font-size: 40px;
        color: #20b757;
        height: 60px;
        width: 60px;
      }
    }
  }
  .ant-card-body {
    padding: 0;
  }
}
