.account-summary {
  background: #fff;
  padding: 14px;
  border-radius: 10px;
  position: relative;
  .header {
    display: flex;
    gap: 10px;
    align-items: center;
    .icon {
      height: 60px;
      width: 60px;
      border: 1px solid #ebecef;
      border-radius: 50%;
      padding: 5px;
      img {
        height: 100%;
        width: 100%;
      }
    }
    .title {
      font-size: 18px;
      font-weight: 400;
    }
    .subtitle {
      font-size: 14px;
      opacity: 50%;
    }
  }

  .more {
    position: absolute;
    top: 30px;
    right: 20px;
    font-size: 20px;
    .ant-dropdown-trigger {
      height: fit-content;
      cursor: pointer;
      height: 30px;
      width: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background: transparent;
      transition: background 0.3s;
    }
    .ant-dropdown-trigger:hover {
      background: #dedede;
    }
  }
  .kpi {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    margin-top: 30px;

    .value-label {
      min-width: 170px;

      .value {
        font-size: 16px;
        font-weight: 500;
        margin-top: 4px;
      }
      .label {
        font-size: 14px;
        opacity: 0.7;
      }
    }
  }
}
