.investment-details-container {
  .actions {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;

    .title {
      font-size: 24px;
      font-weight: 600;
    }
    .buttons {
      display: flex;
      align-items: center;
      gap: 14px;
      .ant-btn {
        font-size: 12px;
        font-weight: 500;
      }
    }
  }
  .forecast {
    background: #fff;
    padding: 14px;
    border-radius: 10px;
    margin-top: 20px;
    .summary-tab {
      .header {
        font-size: 18px;
      }
    }
  }
  .account-cell {
    display: flex;
    align-items: center;
    gap: 10px;
    padding-top: 4px;
    .icon {
      height: 30px;
      width: 30px;
      background: #205cb7;
      text-transform: capitalize;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 22px;
      line-height: 20px;
    }
    .content {
      line-height: 20px;

      .main {
      }
      .meta {
        font-size: 12px;
        opacity: 0.5;
        font-weight: 400;
      }
    }
  }
}
