.banking-container {
  .header {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  .actions {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;

    .title {
      font-size: 24px;
      font-weight: 600;
    }
    .buttons {
      display: flex;
      align-items: center;
      gap: 14px;
      .ant-btn {
        font-size: 12px;
        font-weight: 500;
      }
    }
  }
  .metrics {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  .account {
    margin-top: 40px;
    background: #fff;
    padding: 20px;
    border-radius: 10px;

    .title {
      font-size: 18px;
    }
    .accounts {
      .account-card {
        // display: flex;
        // flex-flow: row wrap;
        // gap: 20px;
        // justify-content: space-between;
        display: grid;
        // grid-template-columns: repeat(auto-fill, 31%);
        grid-gap: 30px;
        justify-content: space-between;
        grid-template-columns: 31% 31% 31%;
      }
      .account-card::after {
        content: "";
        flex: auto;
      }
    }
  }
}

::ng-deep {
  .buttons {
    .ant-btn {
      font-size: 14px;
    }
  }
  .ant-radio-button-wrapper:last-child {
    border-start-end-radius: 30px;
    border-end-end-radius: 30px;
  }
}

.dropdown-option {
  font-size: 12px;
  font-weight: 400;
}

.account-form {
  max-height: 85vh;
  overflow: auto;
}
