.bank-card {
  transition: box-shadow 0.3s ease, border 0.3s ease, background ease 0.5s;

  .ant-card-body {
    padding: 10px 14px;
    .top {
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px;
      .header {
        display: flex;
        gap: 10px;
        .icon {
          height: 40px;
          width: 40px;
          border-radius: 50%;
          overflow: hidden;
          border: 1px solid #ebecef;
          img {
            height: 100%;
            width: 100%;
          }
        }
        .text {
          font-size: 14px;
          .meta {
            font-size: 12px;
            opacity: 0.7;
            font-weight: 400;
          }
        }

        margin-bottom: 10px;
      }
      .ant-dropdown-trigger {
        height: fit-content;
        cursor: pointer;
        height: 30px;
        width: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background: transparent;
        transition: background 0.3s;
      }
      .ant-dropdown-trigger:hover {
        background: #dedede;
      }
    }
    .label-value {
      display: flex;
      gap: 4px;
      font-size: 12px;
      margin-bottom: 6px;
      .label {
        opacity: 0.8;
      }
      .value {
        font-weight: 500;
        min-width: 130px;
      }
    }
    .type {
      position: absolute;
      right: 0;
      bottom: 5px;
      background: #4371e9;
      padding: 0 10px;
      border-top-left-radius: 13px;
      border-bottom-left-radius: 13px;
      color: #fff;
      font-size: 12px;
      text-transform: capitalize;
    }
  }
}
.bank-card:hover {
  cursor: pointer;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border: 1px solid #20b757;
  background: #20b75715;
}
