.account-form-modal {
  .ant-modal-header {
    padding-bottom: 10px;
    border-bottom: 1px dashed #20b75761;
    font-size: 40px;
    margin-bottom: 20px;
    .ant-modal-title {
      font-size: 20px;
      font-weight: 400;
    }
  }
  .ant-form-item {
    .ant-form-item-label {
      padding-bottom: 0;
      label {
        font-size: 14px;
        padding-bottom: 0 !important;
        font-weight: 400;
        min-width: 200px;
        height: 40px;
      }
      input {
      }
    }
    .ant-input {
      padding: 8px 14px;
      background: #4372e90a;
    }
    .ant-input::placeholder {
      font-size: 14px;
    }

    .ant-select {
      height: 40px;
      .ant-select-selector {
        background: #4372e90a;
      }
    }
    .ant-picker {
      height: 40px;
      border-radius: 32px;
      background: #4372e90a;
      width: 100%;
      .ant-picker-input > input {
        font-size: 14px;
      }
    }
    .ant-radio-group {
      padding: 8px 0;
      .ant-radio-wrapper {
        font-size: 14px;
      }
    }
  }
  .ant-form-vertical
    .ant-form-item:not(.ant-form-item-horizontal)
    .ant-form-item-label {
    padding: 8px !important;
    font-weight: 400;
  }
  .submit-button {
    width: 100%;
    padding: 20px 0;
  }
}

.account-form {
  .modal-header {
    font-size: 20px;
  }
}
